@font-face {
  font-weight: 400;
  font-family: "Exo2";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Exo2-Regular.woff2") format("woff2"), url("../fonts/Exo2-Regular.woff") format("woff2");
}

@font-face {
  font-weight: 700;
  font-family: "Exo2";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Exo2-SemiBold.woff2") format("woff2"), url("../fonts/Exo2-SemiBold.woff2") format("woff2");
}

@font-face {
  font-weight: 800;
  font-family: "RoundedMp";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RoundedMplus1c-Black.woff2") format("woff2"), url("../fonts/RoundedMplus1c-Black.woff2") format("woff2");
}

@font-face {
  font-weight: 200;
  font-family: "RoundedMp";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RoundedMplus1c-Thin.woff2") format("woff2"), url("../fonts/RoundedMplus1c-Thin.woff2") format("woff2");
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  font-size: 16px;
}

body {
  box-sizing: border-box;
  color: #fff;
  font-family: "Exo2", sans-serif;
  line-height: 1.5;
  background-color: #d5bff4;
}

@media (min-width: 2100px) {
  body {
    zoom: 1.2;
  }
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "RoundedMp", sans-serif;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.site-container {
  width: 100%;
  max-width: 1772px;
  margin: 0 auto;
  padding: 0 48px;
}

@media (max-width: 1366px) {
  .site-container {
    padding: 0 32px;
  }
}

@media (max-width: 1200px) {
  .site-container {
    padding: 0 24px;
  }
}

@media (max-width: 360px) {
  .site-container {
    padding: 0 16px;
  }
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.page-main {
  flex-grow: 1;
}

.button-reset {
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  border-radius: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.social-list {
  display: flex;
  align-items: center;
  margin: -3px;
}

.social-list__item a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  margin: 3px;
  background-color: #fff;
  border-radius: 9px;
  outline: none;
  transition: background-color 0.2s ease-in;
}

.social-list__item a .icon {
  width: 20px;
  height: 20px;
  fill: #714ca8;
}

@media (min-width: 1025px) {
  .social-list__item a:hover {
    background-color: #f7da5b;
  }
}

.social-list__item a:focus {
  background-color: #f7da5b;
}

.social-list__item a:active {
  transform: scale(0.93);
}

.btn {
  position: relative;
  z-index: 10;
  outline: none !important;
}

.btn span {
  position: relative;
  z-index: 19;
  display: block;
  padding: 23px 46px;
  overflow: hidden;
  color: #836f18;
  font-weight: 700;
  text-transform: uppercase;
  background: #e8b635;
  background: linear-gradient(14deg, #e8b635 9%, #eec237 14%, #f4d13b 75%, #f6d551 81%, #fbe795 88%);
  background-color: #f7da5b;
  border-radius: 54px;
  cursor: pointer;
  transition: all 0.2s ease-in;
}

.btn span::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: #e55d28;
  background: linear-gradient(20deg, #e55d28 5%, #ed7331 12%, #ec7230 18%, #f59148 84%, #f9a972 90%, #fbc9a1 97%);
  border-radius: 20px;
  cursor: pointer;
  opacity: 0;
  transition: all 0.2s ease-in;
  content: "";
}

.btn:focus span {
  color: #fff;
  border-radius: 20px;
}

.btn:focus span::before {
  opacity: 1;
}

@media (min-width: 1025px) {
  .btn:hover span {
    color: #fff;
    border-radius: 20px;
  }
  .btn:hover span::before {
    opacity: 1;
  }
}

.text-center {
  text-align: center;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 70;
  width: 100%;
  padding-bottom: 110px;
  background-repeat: no-repeat;
  background-position: top center;
  background-size: contain;
}

.header__container {
  display: flex;
  align-items: center;
  padding: 21px 0;
}

.header__quantity {
  display: none;
  margin-right: 12px;
  color: #836f18;
  font-size: 0.875rem;
  line-height: 1;
}

.header__logo {
  display: flex;
  align-items: center;
  margin-right: 50px;
  outline: none !important;
}

.header__logo img {
  max-width: 150px;
  margin-right: 79px;
}

.header__logo p {
  max-width: 145px;
  margin: 0;
  color: #fff;
  font-size: 0.875rem;
  line-height: 1.3;
}

.header__logo:focus img {
  transform: scale(1.1);
}

.header__logo:focus p {
  color: #f7da5b;
}

.header__location {
  display: block;
  margin-right: 80px;
  color: #fff;
  font-size: 0.875rem;
}

.header__nav_wrap {
  display: flex;
  align-items: center;
  min-height: 57px;
}

.header__nav ul {
  display: flex;
  padding-top: 16.5px;
  padding-bottom: 16.5px;
  padding-left: 40px;
  background-color: #fff;
  border-radius: 32px 0 0 32px;
}

.header__nav ul li {
  margin-right: 16px;
  padding-right: 16px;
  border-right: 1px solid #dbe3f4;
}

.header__nav ul li a {
  color: #00205c;
  outline: none !important;
  transition: color 0.2s ease-in;
}

@media (min-width: 1025px) {
  .header__nav ul li a:hover {
    color: #f7da5b;
  }
}

.header__nav ul li a:focus {
  color: #f7da5b;
}

.header__nav ul li:nth-last-of-type(1) {
  margin-right: 30px;
  padding-right: 0;
  border-right: none;
}

.header__nav-cart {
  display: flex;
  align-items: center;
  height: 57px;
  padding-right: 17px;
  padding-left: 13px;
  background-color: #f7da5b;
  border-radius: 0 32px 32px 0;
  outline: none !important;
}

.header__nav-cart .icon-cart {
  width: 23px;
  height: 23px;
  color: #fff;
  fill: #836f18;
}

.header__nav-cart:focus {
  background-color: #836f18;
}

.header__nav-cart:focus .header__quantity {
  color: #fff;
}

.header__nav-cart:focus .icon-cart {
  fill: #f7da5b;
}

.header__btn-group {
  margin-left: auto;
  text-align: end;
}

.header__btn-group .phone-content {
  display: block;
  margin-bottom: 8px;
  color: #fff !important;
  font-weight: 700;
  font-size: 1.25rem !important;
  line-height: 1.2;
  outline: none;
}

.header__btn-group .phone-content:focus {
  color: #f7da5b !important;
}

.header__order-button {
  display: inline-block;
  outline: none;
  cursor: pointer;
}

.header__order-button span {
  position: relative;
  color: #fff;
}

.header__order-button span::before {
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  border-bottom: 1px dashed currentColor;
  transition: opacity 0.2s ease-in;
  content: "";
}

@media (min-width: 1025px) {
  .header__order-button span:hover::before {
    opacity: 0;
  }
}

.header__order-button:focus span {
  color: #f7da5b;
}

.header__order-button:focus span::before {
  opacity: 0;
}

