}

.hero-section .layer._sky7 {
  top: 190px;
  right: 0;
  width: 58px;
  height: 190px;
}

.hero-section .layer._sky8 {
  top: 341px;
  right: 0;
  width: 112px;
  height: 142px;
  animation: levitateSmall 6s ease-in-out infinite;
}

.hero-section .layer._sky9 {
