}

.hero-section .layer._ball2 {
  top: 470px;
  left: 450px;
  width: 138px;
  height: 378px;
  animation: levitateOut 6s ease-in-out infinite;
}

.hero-section .layer._ball3 {
  top: 154px;
  right: 660px;
  width: 196px;
  height: 443px;
  animation: levitate 6s ease-in-out infinite;
}

.hero-section .layer._ball4 {
  top: 300px;
  right: 466px;
  width: 183px;
  height: 484px;
  animation: levitateOut 6s ease-in-out infinite;
}

.hero-section .layer._light-right {
  top: 0;
  right: 0;
  z-index: 20;
  width: 817px;
  height: 663px;
}

.hero-section .layer._light-left {
  top: 0;
  left: 0;
  z-index: 50;
