  height: 838px;
}

.hero-section .layer._hero-mountains {
  bottom: -70px;
  left: 0;
  z-index: 50;
  width: 100%;
  height: 460px;
  background-position: bottom center;
  background-size: cover;
}

.hero-section .layer._rabbit {
  bottom: 0;
  left: 630px;
  width: 221px;
  height: 549px;
  perspective: 800px;
  animation: levitateOut 6s ease-in-out infinite;
}

.hero-section .layer._rabbit .rabbit-component {
  position: absolute;
  top: 0;
  left: 0;
  width: 221px;
  height: 549px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.hero-section .layer._rabbit .rabbit {
