  width: 112px;
  height: 299px;
  animation: levitateOut 6s ease-in-out infinite;
}

.hero-section .layer .rabit-eye {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 50;
  width: 20px;
  height: 20px;
  background-color: #fff;
  border-radius: 50%;
}

.hero-section .layer .rabit-eye .dot {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 10px;
  height: 10px;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 50%;
  transform: translate(-50%, -50%);
}

.hero-section .layer .rabit-eye._right {
  top: 96px;
  right: 33px;
  left: auto;
}

.hero-section .layer .rabit-eye._left {
  top: 60px;
  left: 123px;
}

.hero-section__button {
  margin-left: 425px;
}

.hero-section__button span {
  padding: 33px 78px;
}

.hero-section__container {
  position: relative;
  z-index: 90;
  max-width: 961px;
  margin: 0 auto;
}

.hero-section__container h1 {
  margin: 0;
  margin-bottom: 11px;
  font-weight: 200;
  font-size: 6.375rem;
  font-family: "RoundedMp", sans-serif;
  line-height: 1.14;
}

.hero-section__container h1 span {
  font-weight: 800;
}

.hero-section__container h1 span span:nth-of-type(1) {
  display: block;
  padding-left: 67px;
}

.hero-section__container h1 span span:nth-of-type(2) {
  display: block;
  padding-left: 423px;
}

.hero-section__container p {
  margin: 0;
  margin-bottom: 48px;
  padding-left: 426px;
  font-size: 1.25rem;
  line-height: 1.4;
}

@keyframes levitate {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5rem);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes levitateOut {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(4rem);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes levitateX {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-5rem);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes levitateXOut {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(5rem);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes levitateSmall {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(2rem);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes levitateSmallX {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-2rem);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes levitateSmallXOut {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(2rem);
  }
  100% {
    transform: translateX(0);
  }
}

.catalog {
  position: relative;
  z-index: 10;
  margin-top: -54px;
  padding-bottom: 565px;
}

.catalog .layer {
  position: absolute;
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
}

.catalog .layer._bg {
  bottom: 0;
  left: 50%;
  z-index: -1;
  width: 100%;
  height: 1108px;
  transform: translateX(-50%);
}

.catalog .layer._ball1 {
  bottom: 250px;
  left: 130px;
  z-index: 15;
  width: 408px;
  height: 1117px;
  animation: levitateCatalog 6s ease-in-out infinite;
}

.catalog .layer._ball2 {
  bottom: 170px;
  left: 27px;
  width: 251px;
  height: 992px;
  animation: levitateCatalogOut 6s ease-in-out infinite;
}

.catalog .layer._kid-rabbit {
  right: 490px;
  bottom: 270px;
  width: 438px;
  height: 549px;
  animation: levitate 6s ease-in-out infinite;
}

.catalog .layer._kid-rabbit ._kid-rabbit__component {
  position: absolute;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
}

.catalog .layer._kid-rabbit .kid-rabbit-body {
  bottom: 0;
  left: 50%;
  width: 221px;
  height: 400px;
  transform: translateX(-50%);
}

.catalog .layer._kid-rabbit .kid-rabbit-ball1 {
  top: 116px;
  left: -20px;
  width: 132px;
  height: 130px;
  animation: kid-ball1 6s ease-in-out infinite;
}

.catalog .layer._kid-rabbit .kid-rabbit-ball2 {
  top: 0;
  left: 140px;
  width: 121px;
  height: 153px;
  animation: kid-ball2 6s ease-in-out infinite;
}

.catalog .layer._kid-rabbit .kid-rabbit-ball3 {
  top: 46px;
  right: 9px;
  width: 125px;
  height: 147px;
  animation: kid-ball3 6s ease-in-out infinite;
}

.catalog .layer._kid-rabbit .kid-rabbit-toy {
  right: 74px;
  bottom: -2px;
  left: auto;
  width: 107px;
  height: 104px;
  animation: kid-toy 6s ease-in-out infinite;
}

@keyframes kid-ball1 {
  0% {
    transform: rotate(0) translate(0, 0) skewY(0);
  }
  50% {
    transform: rotate(-13deg) translate(-15px, 11px) skewY(3deg);
  }
  100% {
    transform: rotate(0) translate(0, 0) skewY(0);
  }
}

@keyframes kid-ball2 {
  0% {
    transform: rotate(0) translate(0, 0) skewY(0);
  }
  50% {
    transform: rotate(25deg) translate(31px, -16px) skewY(0);
  }
  100% {
    transform: rotate(0) translate(0, 0) skewY(0);
  }
}

@keyframes kid-ball3 {
  0% {
    transform: rotate(0) translate(0, 0) skewY(0);
  }
  50% {
    transform: rotate(24deg) translate(33px, 9px) skewY(-2deg);
  }
  100% {
    transform: rotate(0) translate(0, 0) skewY(0);
  }
}

@keyframes kid-toy {
  0% {
    transform: rotate(0) translate(0, 0) skewY(0);
  }
  50% {
    transform: rotate(-32deg) translate(18px, -3px) skewY(-2deg);
  }
  100% {
    transform: rotate(0) translate(0, 0) skewY(0);
  }
}

.catalog__link {
  align-self: flex-start;
}

.catalog__link span {
  padding: 33px 82px;
}

.catalog__title {
  max-width: 723px;
  margin-bottom: 58px;
}

.catalog__title h2 {
  margin: 0;
  font-weight: 200;
  font-size: 5rem;
  line-height: 1.2;
}

.catalog__title h2 span {
  font-weight: 800;
}

.catalog__container {
  display: flex;
  justify-content: space-between;
}

.catalog__col:nth-child(1) {
  width: 379px;
}

.catalog__col:nth-child(2) {
  display: flex;
  flex-direction: column;
  width: calc(100% - 432px);
}

.catalog__list {
  width: 286px;
  margin-left: auto;
  padding: 30px 0 30px 12px;
  background: linear-gradient(#bea1e8, #d5bff4);
  border-radius: 20px;
  box-shadow: inset 0 1px 0 #8e64cc;
}

.catalog__item {
  position: relative;
  z-index: 10;
  padding: 9px 15px 13px 28px;
  overflow: hidden;
  color: #573e7b;
  font-size: 1.125rem;
  border-radius: 22px 0 0 22px;
  cursor: pointer;
}

.catalog__item._active::before {
  transform: translateX(0);
}

.catalog__item::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: 22px 0 0 22px;
  transform: translateX(100%);
  transform-origin: right;
  transition: transform 0.2s ease-in;
  content: "";
}

.catalog__item:hover::before {
  transform: translateX(0);
}

.catalog__products {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 40px;
}

.catalog__product {
  grid-column: 4 span;
  height: 502px;
  background-color: #fff;
  border-radius: 20px;
}

.catalog__product img {
  position: absolute;
  top: 0;
  left: 50%;
  display: block;
