  align-items: flex-start;
}

.mini-cart__logo span {
  display: block;
  display: -webkit-box;
  overflow: hidden;
  font-size: 1.125rem;
  line-height: 1.2;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.mini-cart__logo:hover span {
  color: #f7da5b;
}

.mini-cart__img {
  width: 110px;
  height: 110px;
  margin-right: 15px;
  object-fit: cover;
  object-position: center;
  background-color: #fff;
  border-radius: 15px;
}

.mini-cart__price {
  flex-shrink: 0;
}

.mini-cart__price span {
  display: block;
  font-size: 1.125rem;
}

.mini-cart__quant {
  flex-shrink: 0;
}

.mini-cart__quant .quant {
  justify-content: center;
  margin: 0 auto;
  padding: 16px 20px;
  border-radius: 15px;
}

.mini-cart__quant .quant__minus, .mini-cart__quant .quant__plus {
  width: 25px;
  height: 25px;
}

.mini-cart__summ {
  flex-shrink: 0;
}

.mini-cart__summ span {
  display: inline-block;
  font-size: 1.125rem;
}

.footer {
  position: relative;
  z-index: 10;
  padding-top: 40px;
  padding-bottom: 90px;
  overflow: hidden;
  background-color: #714ca8;
}

.footer-layer {
  position: absolute;
  bottom: -460px;
  left: 50%;
  z-index: -1;
  width: 2420px;
  height: 687px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  transform: translateX(-50%);
}

.footer__container {
  display: flex;
  justify-content: space-between;
  max-width: 1390px;
  margin: 0 auto;
}

.footer__logo {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
  outline: none !important;
}

.footer__logo img {
  max-width: 150px;
  margin-right: 20px;
}

.footer__logo p {
  max-width: 145px;
  margin: 0;
  color: #fff;
  font-size: 0.875rem;
  line-height: 1.3;
}

.footer__logo:focus img {
  transform: scale(1.1);
}

.footer__logo:focus p {
  color: #f7da5b;
}

.footer__copyright {
  display: inline-block;
  margin-top: auto;
  margin-bottom: 16px;
  color: #fff;
  font-size: 0.875rem;
  line-height: 1.3;
}

.footer__policy {
  position: relative;
  display: inline-block;
  color: #fff;
  outline: none;
}

.footer__policy::before {
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  border-bottom: 1px dashed currentColor;
  transition: opacity 0.2s ease-in;
  content: "";
}

@media (min-width: 1025px) {
  .footer__policy:hover::before {
    opacity: 0;
  }
}

.footer__policy:focus {
  color: #f7da5b;
}

.footer__legend {
  display: block;
  margin-bottom: 20px;
  color: #c7a2ff;
  font-weight: 700;
  font-size: 2rem;
  line-height: 1.2;
}

.footer__nav ul li {
  margin-bottom: 11px;
}

.footer__nav ul li a {
  color: #fff;
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 1.4;
  outline: none !important;
  transition: color 0.2s ease-in;
}

@media (min-width: 1025px) {
  .footer__nav ul li a:hover {
    color: #f7da5b;
  }
}

.footer__nav ul li a:focus {
  color: #f7da5b;
}

.footer__nav ul li:nth-last-of-type(1) {
  margin-bottom: 0;
}

.footer__social-list {
  display: flex;
  align-items: center;
}

.footer__location {
  display: block;
  max-width: 194px;
  color: #fff;
  font-size: 1.125rem;
}

.footer .b2b {
  margin-top: auto;
  color: #fff;
}

.footer__left {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 288px;
  margin-right: 73px;
}

.footer__right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  max-width: 276px;
}

.footer__right .phone-content {
  margin-bottom: 14px;
  color: #fff;
  font-weight: 700;
  font-size: 2rem !important;
  outline: none !important;
  transition: color 0.2s ease-in;
}

@media (min-width: 1025px) {
  .footer__right .phone-content:hover {
    color: #f7da5b !important;
  }
}

.footer__right .phone-content:focus {
  color: #f7da5b !important;
}

.footer__content {
  display: flex;
  flex-grow: 1;
  margin-right: 73px;
}

.footer__col:nth-of-type(1) {
  margin-right: 30px;
}

.footer__col:nth-of-type(2) {
  margin-right: 48px;
}
